import React, { Fragment } from 'react';
import {
  H1,
  H2,
  H3,
  Content,
  InfoText,
  CodeArea,
  CodeComment,
  CodeFrame,
} from 'components/styles';

const Basics = () => (
  <Fragment>
    <H1>2. <span>Bsico</span></H1>
    <Content>
      <InfoText>
        Os cdigos comeam por aqui! Nesse captulo, mostro conceitos bsicos e fundamentais ao JavaScript. Note que
        os exemplos esto no padro &quot;antigo&quot; <strong>(pr-ES6)</strong>, as diferenas sero explicadas
        depois. Para aqueles que no esto familiarizados, a linguagem passou por&nbsp;
        <strong>evolues tcnicas e sintticas</strong> que flexibilizaram e limitaram problemas que eram causados por
        diferenas semnticas entre JS e outras linguagens de programao.
        <br /><br />
        Aqui, so exemplificadas&nbsp;
        <strong>operaes, atribuies, tipos de dados, estruturas e caractersticas de interpretao</strong>&nbsp;
        que so essenciais para entender o funcionamento e o comportamento dos cdigos. Aproveitei para enfatizar,
        tambm, alguns pontos que so considerados <strong>legado ou antipadro</strong>, e como eles so contornados
        pelo ES6 em diante.
      </InfoText>
      <H2>2.1. Hello World</H2>
      <CodeArea>
        <CodeComment>
          Reza a lenda que se voc aprender uma linguagem sem fazer um <strong>&quot;Hello World!&quot;</strong> uma
          maldio  passada para sua vida. Felizmente,  to simples quanto chamar o mtodo&nbsp;
          <strong>console.log();</strong>
        </CodeComment>
        <CodeFrame
          title="basics/1_hello.js"
          width="100%"
          height="auto"
          src="//jsfiddle.net/santiagogustavo/b1an8Lf7/2/embedded/js,result/dark/"
          frameBorder="0"
        />
      </CodeArea>
      <H2>2.2. Operadores</H2>
      <CodeArea>
        <CodeComment>
          As operaes <strong>algbricas e booleanas</strong> funcionam como esperado (e com sintaxe muito familiar).
          No exemplo, as operaes so aplicadas em valores <strong>number e boolean</strong>, mas ser mostrado adiante
          que  possvel fazer entre diversos tipos de dados.
          <H3>== vs ===</H3>
          Observe que nas comparao ao lado h o uso de <strong>===</strong>. Este operador pode parecer um pouco
          estranho, mas sua utilidade  muito grande. Alm de comparar os valores, como o operador&nbsp;
          <strong>==</strong> tradicionalmente faz, o <strong>=== (tambm chamado de eqeqeq)</strong>&nbsp;
          compara os tipos de dados dos operandos.
          <br /><br />
          <strong>
            De fato, a utilidade do ===  to grande que ele se tornou padro na linguagem e deve ser utilizado sempre
            que for necessrio comparar dois valores.
          </strong>
        </CodeComment>
        <CodeFrame
          title="basics/2_operators.js"
          width="100%"
          height="auto"
          src="//jsfiddle.net/santiagogustavo/gq0uwamt/1/embedded/js,result/dark/"
          frameBorder="0"
        />
      </CodeArea>
      <H2>2.3. Comentrios</H2>
      <CodeArea>
        <CodeComment>
          Assim como nas demais linguagens,  possvel <strong>(e recomendado)</strong> colocar comentrios nos cdigos.
          Novamente, a sintaxe  muito familiar e funciona como esperado, onde os comentrios podem ser colocados&nbsp;
          <strong>aps as expresses em uma linha, em uma linha separada ou ocupar vrias linhas</strong>.
        </CodeComment>
        <CodeFrame
          title="basics/3_comments.js"
          width="100%"
          height="auto"
          src="//jsfiddle.net/santiagogustavo/fhse2br5/embedded/js,result/dark/"
          frameBorder="0"
        />
      </CodeArea>
      <H2>2.4. Tipos de dados</H2>
      <CodeArea>
        <CodeComment>
          Existem duas &quot;classes&quot; de tipos de dados:
          <ul>
            <li>
              Os tipos <strong>primitivos</strong> so tratados como entidades <strong>imutveis e bsicas</strong>, nas
              quais os valores j esto definidos previamente e no podem ser modificados.
            </li>
            <li>
              O tipo <strong>Object</strong>  utilizado para definir outras &quot;macros&quot; na linguagem, tanto a
              nvel de implementao <strong>(estruturas presentes na linguagem)</strong> quanto a nvel de usurio
              <strong>(objetos, classes e entidades criadas em cdigo).</strong>
            </li>
          </ul>
          No exemplo, so verificados os tipos de alguns valores <strong>primitivos e Object</strong>, sendo eles:
          <ul>
            <li>
              <strong>String:</strong> valores textuais
            </li>
            <li>
              <strong>Number:</strong> valores numricos reais
            </li>
            <li>
              <strong>Boolean:</strong> true/false
            </li>
            <li>
              <strong>Object:</strong> estruturas de objeto, instncias de classe, macros, entre outros
            </li>
            <li>
              <strong>Function:</strong> funes
            </li>
            <li>
              <strong>Null:</strong> valor nulo (diferente de undefined)
            </li>
            <li>
              <strong>Undefined:</strong> entidade indefinida (sem valor)
            </li>
          </ul>
          Observe que <strong>Array</strong>  um valor do tipo <strong>Object</strong>. Esta  uma macro da linguagem!
          <H3>typeof e instanceof</H3>
          Estes operadores so utilizados para indicar <strong>tipo ou instncia de classe</strong> para valores e
          variveis. Use-os sempre que estiver na dvida, tambm so teis para verificar&nbsp;
          <strong>valores indefinidos, objetos vazios e outras condies &quot;tipadas&quot;.</strong>
          <H3>Concatenao entre tipos</H3>
          Aqui as coisas ficam um tanto confusas: alguns tipos podem ser concatenados para gerar novos valores, mas no
           recomendado, porm, que isso seja feito intuitivamente pelo operador <strong>&quot;+&quot;</strong>.&nbsp;
          <strong>Para ilustrar ainda melhor: </strong>
          <br /><br />
          <iframe
            title="destroyallsoftware wat"
            width="100%"
            height="315"
            src="https://www.youtube.com/embed/BrB0oaSz8Nk"
            frameBorder="0"
          />
        </CodeComment>
        <CodeFrame
          title="basics/4_datatypes.js"
          width="100%"
          height="auto"
          src="//jsfiddle.net/santiagogustavo/hyemn3fo/embedded/js,result/dark/"
          frameBorder="0"
        />
      </CodeArea>
    </Content>
  </Fragment>
);

export default Basics;



